@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');

.body {
    font-family: 'Roboto', sans-serif;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    margin: 0;

    // 通过设定固定的图片大小从而实现图片的滑动效果
    img {
        width: 500px;
        height: 500px;
        object-fit: cover;
    }
}

.carousel {
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
    height: 530px;
    width: 500px;
    overflow: hidden;
}

.imageContainer {
    display: flex;
    transform: translateX(0);
    transition: transform 0.5s ease-in-out;
}


.buttonsContainer {
    display: flex;
    justify-content: space-between;
}

.btn {
    background-color: #1890ff;
    color: #fff;
    border: none;
    padding: 0.5rem;
    cursor: pointer;
    width: 49.5%;
}

.btn:hover {
    opacity: 0.9;
}

.btn:focus {
    outline: none;
}